import React, { useEffect, useState } from "react";
import { Form, Input, Button, Upload, message, Row, Col, Card } from "antd";
import { UploadOutlined } from "@ant-design/icons";
import Cookies from "js-cookie";
import CryptoJS from "crypto-js";
import axios from "axios";
const AdminProfile = () => {
  const [form] = Form.useForm();
  const [profileData, setProfileData] = useState({});
  const [logo, setLogo] = useState(null);
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    : null;
  // Handle file upload (Logo)
  const handleLogoUpload = async (file) => {
    setLogo(file);
    try {
      const formData = new FormData();
      formData.append("businesslogo", file);

      const config = {
        url: `/admin/uploadbusineess`,
        method: "post",
        baseURL: "https://majesticmall.net/api",
        headers: {
          Authorization: `Bearer ${token}`,
          // Let axios automatically set the 'Content-Type' header to 'multipart/form-data'
        },
        data: formData,
      };

      let res = await axios(config);
      if (res.status === 200) {
        setProfileData(res.data.profile);
        message.success("Profile updated successfully!");
      }
    } catch (error) {
      console.error("Error uploading logo:", error);
      if (error.response) {
        console.error("Response Data:", error.response.data);
        console.error("Response Status:", error.response.status);
        console.error("Response Headers:", error.response.headers);
      } else if (error.request) {
        console.error("Request:", error.request);
      } else {
        console.error("Error Message:", error.message);
      }
      message.error("Failed to update");
    }
    return false; // Prevent auto upload
  };

  const getprofile = async () => {
    try {
      let res = await axios.get(
        "https://majesticmall.net/api/admin/getbussinessprofile"
      );
      if (res.status == 200) {
        setProfileData(res.data.profile);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getprofile();
  }, []);

  const { TextArea } = Input;
  // console.log("profile",profileData);

  // Handle form submission
  const onFinish = async (values) => {
    try {
      const config = {
        url: `/admin/uploadbusineess`,
        method: "post",
        baseURL: "https://majesticmall.net/api",
        hearder: { "content-type": "multipart/form-data" },
        headers: {
          Authorization: `Bearer ${token}`,
        },
        data: { ...values, logo: logo },
      };
      let res = await axios(config);
      if (res.status == 200) {
        setProfileData(res.data.profile);
        message.success("Profile updated successfully!");
      }
    } catch (error) {
      console.log(error);
      message.error("Failed to update");
    }
  };

  return (
    <div style={{ padding: "20px" }}>
      <Row gutter={32}>
        {/* Form Side */}
        <Col xs={24} md={12}>
          <h2>Update Business Details</h2>
          <Form
            form={form}
            name="profile_management"
            onFinish={onFinish}
            initialValues={profileData}
            layout="vertical"
          >
            {/* Business Logo */}
            <Form.Item label="Business Logo" valuePropName="fileList">
              <Upload
                beforeUpload={handleLogoUpload}
                showUploadList={false}
                accept="image/*"
              >
                <Button icon={<UploadOutlined />}>Click to Upload Logo</Button>
              </Upload>
              {logo && (
                <img
                  src={URL.createObjectURL(logo)}
                  alt="logo"
                  width={100}
                  style={{ marginTop: 10 }}
                  onError={(e) => {
                    e.target.src = "../img/example.jpg"; // Replace with your default image URL
                  }}
                />
              )}
            </Form.Item>

            {/* Business Name */}
            <Form.Item
              label="Business Name"
              name="businessName"
              rules={[{ message: "Please enter your business name!" }]}
            >
              <Input />
            </Form.Item>

            {/* Address */}
            <Form.Item
              label="Address"
              name="address"
              rules={[
                {
                  message: "Please enter your business address!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            {/* Phone Number */}
            <Form.Item
              label="Phone Number"
              name="phoneNumber"
              rules={[{ message: "Please enter your phone number!" }]}
            >
              <Input />
            </Form.Item>

            {/* Email */}
            <Form.Item
              label="Email"
              name="email"
              rules={[
                { message: "Please enter your email!" },
                {
                  type: "email",
                  message: "Please enter a valid email address!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            {/* Business ID */}
            <Form.Item
              label="Business ID"
              name="businessId"
              rules={[{ message: "Please enter your business ID!" }]}
            >
              <Input />
            </Form.Item>

            {/* GST Number */}
            <Form.Item
              label="GST Number"
              name="gstNumber"
              rules={[{ message: "Please enter your GST number!" }]}
            >
              <Input />
            </Form.Item>
            <Form.Item
              label="Business Description"
              name="description"
              rules={[
                { message: "Please provide a business description!" },
                {
                  max: 500,
                  message: "Description must not exceed 500 characters.",
                },
              ]}
            >
              <TextArea
                rows={4}
                placeholder="Enter your business description here..."
              />
            </Form.Item>
            <Form.Item>
              <Button
                type="primary"
                htmlType="submit"
                style={{ width: "100%" }}
              >
                Update Profile
              </Button>
            </Form.Item>
          </Form>
        </Col>

        {/* Preview Side */}
        <Col xs={24} md={12}>
          <Card title="Profile Preview" bordered={true}>
            <div style={{ marginBottom: "16px", textAlign: "center" }}>
              {profileData.businesslogo ? (
                <img
                  src={`https://majesticmall.net/profile/${profileData.businesslogo}`}
                  alt="Business Logo"
                  width={120}
                  height={120}
                  //   loading="lazy"
                  style={{ borderRadius: "50%", objectFit: "cover" }}
                  onError={(e) => {
                    e.target.src = "../img/example.jpg"; // Replace with your default image URL
                  }}
                />
              ) : (
                <div
                  style={{
                    width: 120,
                    height: 120,
                    borderRadius: "50%",
                    backgroundColor: "#f0f0f0",
                    lineHeight: "120px",
                    textAlign: "center",
                    fontSize: 14,
                    color: "#888",
                  }}
                >
                  No Logo
                </div>
              )}
            </div>
            <p>
              <strong>Business Name:</strong>{" "}
              {profileData.businessName || "N/A"}
            </p>
            <p>
              <strong>Address:</strong> {profileData.address || "N/A"}
            </p>
            <p>
              <strong>Phone Number:</strong> {profileData.phoneNumber || "N/A"}
            </p>
            <p>
              <strong>Email:</strong> {profileData.email || "N/A"}
            </p>
            <p>
              <strong>Business ID:</strong> {profileData.businessId || "N/A"}
            </p>
            <p>
              <strong>GST Number:</strong> {profileData.gstNumber || "N/A"}
            </p>
            <p>
              <strong>Business description </strong>
              {profileData?.description || "N/A"}
            </p>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default AdminProfile;
